<template>
  <div class="pay">
    <top></top>
    <div class="pay_rule">
      <div>
        <span class="pay_she">
			    <i class="iconfont icon-yingyezhizhao"></i>
			    <span class="pay_img"><img src="../../assets/business-.png"/></span>
			  </span>
        <br />
        <span class="pay_tishi">需要真实有效的营业执照</span>
      </div>
      <div>
        <span class="pay_she">
          <i class="iconfont icon-daozhang"></i>
          <span class="pay_img"><img src="../../assets/account.png"/></span>
        </span>
        <br />
        <span class="pay_tishi">交易完成后钱款立即到账</span>
      </div>
      <div>
        <span class="pay_she">
          <i class="iconfont icon-renzheng"></i>
          <span class="pay_img"><img src="../../assets/charge.png"/></span>
        </span>
        <br />
        <span class="pay_ti">微信支付平台对每笔交易收取 0.6% 手续费</span>
      </div>
      <div>
        <span class="pay_she">
          <i class="iconfont icon-shouxufeishuai"></i>
          <span class="pay_img"><img src="../../assets/certification.png"/></span>
        </span>
        <br />
        <span class="pay_tishi">西瓜科技不收取任何手续费</span>
      </div>
    </div>
    <div class="pay_set">
      <span class="pay_binding">绑定商户</span>
      <div>
        <div>
          <h2 class="h2_shang">商户号</h2>
          <input type="text" class="input_shang" v-model.trim="num" style="width: 2.5rem" placeholder="请输入十位商户号" @focus="textF" @blur="change"><br>
          <span style="color: #e64751;margin-top: 0.1rem;display: inline-block">{{numMsg}}</span>
        </div>
        <div style="margin: 0 0.15rem">
          <h2 class="h2_shang">API密匙</h2>
          <input type="password" class="input_shang" v-model.trim="name" style="width: 2.5rem" placeholder="请输入API密室" @focus="textF" @blur="input"><br>
          <span style="color: #e64751;margin-top: 0.1rem;display: inline-block">{{nameMsg}}</span>
        </div>
        <div>
          <h2></h2>
          <button href="javaScript:void(0)" class="pay_subBtn" @click="bound" v-if="show">绑定账户</button>
          <button href="javaScript:void(0)" class="pay_subBtn" @click="cancelBound" v-if="!show">解绑账户</button>
        </div>
      </div>
      <p class="pay_btn">
            连接后请登入微信小程序后台进行配置，
      <a href="javascript:void(0)">了解如何配置</a>
      </p>
    </div>

  </div>
</template>
<script>
  import top from '../top.vue'
  export default {
    data() {
      return {
        num: '',
        numMsg: "",
        name: '',
        nameMsg: "",
        show: true
      }
    },
    methods: {
      //        绑定账户
      bound() {
        this.post('custom/Own/setUserWxKey', {
          mchid: this.num,
          mkey: this.name
        }).then(res => {
          if(res.data.code === 10000) {
            const content = res.data.msg_test
            this.$Modal.session({
              title: "绑定成功",
              content: content
            });
          } else if(res.data.code === 10003) {
            const content = "你还没设置小程序,暂时无法设置支付"
            this.$Modal.warning({
              title: "设置小程序",
              content: content
            });

          } else {
            const content = res.data.msg_test
            this.$Modal.error({
              title: "设置小程序",
              content: content
            });
          }
        })
      },
      //			解绑账号
      cancelBound() {
        this.post('custom/Own/clearWxKey', {}).then(res => {
          if(res.data.code === 10000) {
            const content = res.data.msg_test
            this.$Modal.session({
              title: "解绑成功",
              content: content
            });
          } else {
            const content = res.data.msg_test
            this.$Modal.error({
              title: "解绑失败",
              content: content
            });
          }
        })
      },
      textF() {
        event.target.style.borderColor = 'skyblue';
      },
      change() {
        if(this.num) {
          this.numMsg = '';
          event.target.style.borderColor = 'gainsboro';
        } else {
          this.numMsg = '请输入十位商户号';
          event.target.style.borderColor = '#e64751';
        }
      },
      input() {
        if(this.name) {
          this.nameMsg = '';
          event.target.style.borderColor = 'gainsboro';
        } else {
          this.nameMsg = '请输入API密钥';
          event.target.style.borderColor = '#e64751';
        }
      }
    },
    mounted() {
      this.post("custom/Own/getWxKey", {}).then(res => {
        if(res.data.code === 10000) {
          this.show = false;
          this.num = res.data.data.mchid;
          this.name = res.data.data.mkey
        } else if(res.data.code === 10003) {
          const content = "你还没设置小程序,暂时无法设置支付"
          this.$Modal.warning({
            title: "设置小程序",
            content: content
          });
        } else {
          const content = "res.data.msg_test"
          this.$Modal.error({
            title: "设置小程序",
            content: content
          });
        }
      })
    },
    components: {
      top
    }
  }
</script>
<style scoped="scoped">
  .pay_rule {
    padding: 0.2rem 0;
    display: flex;
    
  }
  
  .pay_rule>div {
    position: relative;
    border-radius: 0.04rem;
    flex: 1.16rem;
    /*height: 1rem;*/
    background: white;
    box-shadow: 5px 5px 10px #E7E7E7;
    margin: 0.1rem 0.22rem 0 0;
    padding-bottom: 0.05rem;
  }
  
  .h2_shang {
    font-size: 0.13rem;
    font-weight: 600;
    margin-bottom: 0.05rem;
  }
  
  .input_shang {
    display: inline-block;
    width: 3rem;
    border-radius: 0.05rem;
    border: 1px solid gainsboro;
    padding: 0.1rem 0.1rem;
  }
  
  .pay_subBtn {
    color: white;
    margin-top: 0.23rem !important;
    display: inline-block;
    padding: 0.11rem 0.3rem;
    border-radius: 0.05rem;
    background-color: #59A2ED ;
    border: none;
    outline: none;
  }
  
  .pay_subBtn:hover {
    opacity: 0.5;
  }
  
  p {
    padding: 0.2rem 0;
  }
  
  .pay_btn {
    padding: 0 0.3rem 0.2rem;
    letter-spacing:0.06rem;
    color: #666666;
    font-size: 0.14rem;
  }
  
  .pay_she {
    margin: 0.1rem 0 0 0.3rem;
    display: inline-block;
    height: 0.36rem;
    width: 0.36rem;
    background-color: #E3F5FD;
    border-radius: 50%;
    text-align: center;
    line-height: 0.36rem;
  }
  
  .pay_she>i {
    color: #25B3EF;
  }
  
  .pay_tishi {
    z-index: 99;
    margin: 0.2rem 0.2rem 0 0.3rem;
    display: inline-block;
    color: #606060;
    padding-bottom: 0.1rem;
    letter-spacing: 0.06rem;
  }
  
  .pay_ti {
    margin: 0.2rem 0.2rem 0 0.3rem;
    display: inline-block;
    letter-spacing: 0.03rem;
    color: ##606060;
  }
  
  .pay_set{
    background: white;
    box-shadow: 5px 5px 10px #E7E7E7;
    border-radius: 0.04rem;
  }
  
  .pay_set>div {
    display: flex;
    margin: 0 0.1rem 0.1rem 0.32rem;
  }
  
  .pay_binding {
    display: inline-block;
    width: 0.8rem;
    text-align: center;
    margin: 0.2rem 0.1rem 0.1rem 0.32rem;
    color: #111111;
    border-left: 0.03rem solid #25B3EF;
    font-size: 0.15rem;
    font-weight: 530;
    border-top-left-radius: 0.03rem;
    border-bottom-left-radius: 0.03rem;
  }
  .pay_img{
    position: absolute;
    right: 0.4rem;
    top: 0;
    z-index: 9;
  }
</style>



